/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Suspense, lazy } from 'react';
import _ from 'lodash';

import SLDSSection from './SLDSSection';
import GRAXObjectCombobox from './GRAXObjectCombobox';
import GRAXFilterGeneration from './GRAXFilterGeneration';

import {Button, Checkbox, Tooltip, Icon, Alert, RadioButtonGroup, Radio, Spinner} from '@salesforce/design-system-react';

import {orgid} from '../lib/grax-data-lake';

const GRAXFieldsCombobox = lazy(() => import('./GRAXFieldsCombobox'));
const GRAXRestoreDataTable = lazy(() => import('./GRAXRestoreDataTable'));

const ELASTIC_SORTABLE_TYPES = ['text', 'date', 'number'];

const BACKEDUP = 'Backed Up';
const ARCHIVED = 'Archived';
const BOTH = 'Both';

class GRAXRestoreArchiveFilter extends React.Component {
	constructor(props) {
        super(props);
        
		this.state = {
            checked: BOTH
        };
    }

    onChangeHandler(val) {
        this.setState({
            checked: val
        });

        if(this.props.onChange) {
            this.props.onChange(val);
        }
    }
    
    render() {
        const options = [BACKEDUP, ARCHIVED, BOTH];
        
		return (
            <div className="slds-p-bottom_medium">
                <div className="slds-clearfix">
                    <div className="slds-float_left slds-p-right_xx-small">
                        <RadioButtonGroup
                            onChange={(event) => this.onChangeHandler(event.target.value)}
                            //disabled={this.props.disabled}
                            required={this.props.required}
                            name={this.props.name}
                            errorId={this.props.errorId}
                            disabled={this.props.disabled === true}
                        >
                            {_.map(options, function(option, i) {
                                return (
                                    <Radio
                                        key={'option' + i}
                                        id={'option' + i}
                                        label={option}
                                        value={option}
                                        checked={this.state.checked === option}
                                        variant="button-group"
                                    />
                                );
                            }.bind(this))}
                        </RadioButtonGroup>
                    </div>

                    <div className="slds-float_left" style={{
                        margin: '0.41rem'
                    }}>
                        <Tooltip
                            align="top left"
                            content={'Select one of the options, "Backed Up", "Archived", or "Both", below to display these records.'}
                            variant="learnMore"
                        />
                    </div>
                </div>
            </div>
		);
	}
}


export default class GRAXRestore extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            selectedDataIndex: null,
            retrieved: false,
            retrievedCounter: 0,
            _sfRequestsDisabled: true,
            _restoreChildrens: false,
            _checkIsUserActive: true,
            _useGRAXHistoryOnly: false,
            _updateRecordIfExist: false,
            _sendNotification: false,
            _currentOrgRecords: true,
            _graxFields: null,
            _graxFieldsSelection: null,
            _graxFieldsSelectionList: null,
            _filterFunction: ((dataList) => dataList),
            _getFilterQuery: (() => null),
            _filterValuesFields: [],
            _graxSortableFields: null,
            objectHasCustomMapping: false,
            _archiveFilterQuery: null
        }
    }

    hasFieldSelected() {
        return this.state._graxFields != null && this.state._graxFieldsSelectionList != null && this.state._graxFieldsSelectionList.length > 0;
    }

    getFilterQuery() {
        var queryParts = _.concat(this.state._getFilterQuery(), this.state._archiveFilterQuery);

        if(this.state._currentOrgRecords) {
            queryParts.push('(doc.graxorgid.keyword: "' + orgid + '")');
        }
        
        queryParts = _.remove(queryParts, function(queryPart) {
            return queryPart != null;
        });

        if(queryParts && queryParts.length > 0) {
            return queryParts.join(' AND ');
        }

        return null;
    }
    
    render() {
        return (
            <div>
                <SLDSSection isCollapsible={true} isOpen={true} title="Disclaimers">
                    <ol>
                        <li>{'When restoring records in an environment please confirm that the following is true:'}</li>
                        <ol className="slds-list_ordered">
                            <li>{'Ensure that the destination environment has the same customizations and metadata as the source environment'}</li>
                            <li>{'Ensure that all workflows, APEX triggers, validation rules, process builders, duplication rules, etc. are disabled in the destination environment'}</li>
                            <li>{'If restoring in a partial or developer Sandbox, ensure that there is ample storage space for the data you are intending to restore in the destination environment'}</li>
                            <li>{'Ensure that your user has "modify all data" permission in the destination environment'}</li>
                            <li>{'If you are attempting to restore records where the owner is inactive, ensure that your user has the permission "Update records with inactive owners" set to true in the destination environment'}</li>
                        </ol>
                    </ol>

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <SLDSSection isCollapsible={true} isOpen={true} title="Settings">
                    <div className="slds-grid slds-wrap">
                        <div className="slds-col slds-size_1-of-1 slds-medium-size_6-of-12 slds-large-size_4-of-12">
                            <div className="slds-p-bottom_small">
                                <Tooltip
                                    align="top left"
                                    content="When disabled, only the parent record will be restored. When enabled, the parent and all related children will be enabled."
                                >
                                    <a href="javascript:void(0)" style={{marginRight: '0.5rem'}}>
                                        <Icon
                                            assistiveText={{ label: 'Restore Child Records?' }}
                                            category="utility"
                                            name="info"
                                            size="x-small"
                                        />
                                    </a>
                                </Tooltip>
                                <Checkbox
                                    className="inline-block"
                                    defaultChecked={this.state._restoreChildrens}
                                    onChange={(isChecked) => {
                                        this.setState({
                                            _restoreChildrens: isChecked
                                        });
                                    }}
                                    assistiveText={{
                                        label: "Restore Child Records?"
                                    }}
                                    variant="toggle"
                                    labels={{
                                        toggleDisabled: 'Restore Parent Record Only',
                                        toggleEnabled: 'Restore Child Records'
                                    }}
                                />
                            </div>
                        </div>

                        <div className="slds-col slds-size_1-of-1 slds-medium-size_6-of-12 slds-large-size_4-of-12">
                            <div className="slds-p-bottom_small">
                                <Tooltip
                                    align="top left"
                                    content="When disabled, the restore process queries your storage facility to determine if this record exists already in this SF environment. When enabled the process queries SF to see if this record already exists and will not create a duplicate."
                                >
                                    <a href="javascript:void(0)" style={{marginRight: '0.5rem'}}>
                                        <Icon
                                            assistiveText={{ label: 'Skip Salesforce Queries?' }}
                                            category="utility"
                                            name="info"
                                            size="x-small"
                                        />
                                    </a>
                                </Tooltip>
                                <Checkbox
                                    className="inline-block"
                                    defaultChecked={!this.state._sfRequestsDisabled}
                                    onChange={(isChecked) => {
                                        this.setState({
                                            _sfRequestsDisabled: !isChecked
                                        });
                                    }}
                                    assistiveText={{
                                        label: "Skip Salesforce Queries?"
                                    }}
                                    variant="toggle"
                                    labels={{
                                        toggleDisabled: 'Query storage facility',
                                        toggleEnabled: 'Query Salesforce'
                                    }}
                                />
                            </div>
                        </div>

                        <div className="slds-col slds-size_1-of-1 slds-medium-size_6-of-12 slds-large-size_4-of-12">
                            <div className="slds-p-bottom_small">
                                <Tooltip
                                    align="top left"
                                    content="When enabled, the user must have the setting &quot;Update Records with Inactive Owners&quot; set to true otherwise records assigned to an inactive user will NOT be created."
                                >
                                    <a href="javascript:void(0)" style={{marginRight: '0.5rem'}}>
                                        <Icon
                                            assistiveText={{ label: 'Assign Records to Inactive Users?' }}
                                            category="utility"
                                            name="info"
                                            size="x-small"
                                        />
                                    </a>
                                </Tooltip>
                                <Checkbox
                                    className="inline-block"
                                    defaultChecked={!this.state._checkIsUserActive}
                                    onChange={(isChecked) => {
                                        this.setState({
                                            _checkIsUserActive: !isChecked
                                        });
                                    }}
                                    assistiveText={{
                                        label: "Assign Records to Inactive Users?"
                                    }}
                                    variant="toggle"
                                    labels={{
                                        toggleDisabled: 'Assign records only to Active Users',
                                        toggleEnabled: 'Assign records to Active or Inactive Users'
                                    }}
                                />
                            </div>
                        </div>

                        <div className="slds-col slds-size_1-of-1 slds-medium-size_6-of-12 slds-large-size_4-of-12">
                            <div className="slds-p-bottom_small">
                                <Tooltip
                                    align="top left"
                                    content="Set it to TRUE if you don't want to double check that the restored records still exist in the destination Org."
                                >
                                    <a href="javascript:void(0)" style={{marginRight: '0.5rem'}}>
                                        <Icon
                                            assistiveText={{ label: 'Use GRAXHistory only?' }}
                                            category="utility"
                                            name="info"
                                            size="x-small"
                                        />
                                    </a>
                                </Tooltip>
                                <Checkbox
                                    className="inline-block"
                                    defaultChecked={this.state._useGRAXHistoryOnly}
                                    onChange={(isChecked) => {
                                        this.setState({
                                            _useGRAXHistoryOnly: isChecked
                                        });
                                    }}
                                    assistiveText={{
                                        label: "Use GRAXHistory only?"

                                    }}
                                    variant="toggle"
                                    labels={{
                                        toggleDisabled: 'Query SFDC to confirm record exists',
                                        toggleEnabled: 'Query only GRAX History'
                                    }}
                                />
                                
                            </div>
                        </div>

                        <div className="slds-col slds-size_1-of-1 slds-medium-size_6-of-12 slds-large-size_4-of-12">
                            <div className="slds-p-bottom_small">
                                <Tooltip
                                    align="top left"
                                    content="Set it to TRUE if you want to update the previously restored records in the destination Org."
                                >
                                    <a href="javascript:void(0)" style={{marginRight: '0.5rem'}}>
                                        <Icon
                                            assistiveText={{ label: 'Update records?' }}
                                            category="utility"
                                            name="info"
                                            size="x-small"
                                        />
                                    </a>
                                </Tooltip>
                                <Checkbox
                                    className="inline-block"
                                    defaultChecked={this.state._updateRecordIfExist}
                                    onChange={(isChecked) => {
                                        this.setState({
                                            _updateRecordIfExist: isChecked
                                        });
                                    }}
                                    assistiveText={{
                                        label: "Update records?"
                                    }}
                                    variant="toggle"
                                    labels={{
                                        toggleDisabled: 'Only insert records',
                                        toggleEnabled: 'Update records if exists'
                                    }}
                                />
                            </div>
                        </div>

                        <div className="slds-col slds-size_1-of-1 slds-medium-size_6-of-12 slds-large-size_4-of-12">
                            <div className="slds-p-bottom_small">
                                <Tooltip
                                    align="top left"
                                    content="Notify User via Email when Process Completes"
                                >
                                    <a href="javascript:void(0)" style={{marginRight: '0.5rem'}}>
                                        <Icon
                                            assistiveText={{ label: 'Send Notification?' }}
                                            category="utility"
                                            name="info"
                                            size="x-small"
                                        />
                                    </a>
                                </Tooltip>
                                <Checkbox
                                    className="inline-block"
                                    defaultChecked={this.state._sendNotification}
                                    onChange={(isChecked) => {
                                        this.setState({
                                            _sendNotification: isChecked
                                        });
                                    }}
                                    assistiveText={{
                                        label: "Send Notification?"
                                    }}
                                    variant="toggle"
                                    labels={{
                                        toggleDisabled: 'Don\'t Send Notification',
                                        toggleEnabled: 'Send Notification'
                                    }}
                                />
                            </div>
                        </div>

                        <div className="slds-col slds-size_1-of-1 slds-medium-size_6-of-12 slds-large-size_4-of-12">
                            <div className="slds-p-bottom_small">
                                <Tooltip
                                    align="top left"
                                    content="Show records only from the current environment or all environments."
                                >
                                    <a href="javascript:void(0)" style={{marginRight: '0.5rem'}}>
                                        <Icon
                                            assistiveText={{ label: 'Filter records?' }}
                                            category="utility"
                                            name="info"
                                            size="x-small"
                                        />
                                    </a>
                                </Tooltip>
                                <Checkbox
                                    className="inline-block"
                                    defaultChecked={!this.state._currentOrgRecords}
                                    onChange={(isChecked) => {
                                        this.setState({
                                            _currentOrgRecords: !isChecked,
                                            retrievedCounter: this.state.retrievedCounter + 1
                                        });
                                    }}
                                    assistiveText={{
                                        label: "Show records from all environments?"
                                    }}
                                    variant="toggle"
                                    labels={{
                                        toggleDisabled: 'Current Environment',
                                        toggleEnabled: 'All connected SF environments'
                                    }}
                                />
                            </div>
                        </div>
                    </div>

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <SLDSSection isCollapsible={true} isOpen={true} title="Select the GRAX Table">
                    <GRAXObjectCombobox
                        onSelect={(event, data) => {
                            var objectHasCustomMapping = false;

                            var selectedDataIndex = data[0];

                            console.log(this.props.customMapping, selectedDataIndex);

                            var objectType = selectedDataIndex.label;

                            if(this.props.customMapping != null) {
                                var objectTypeMapping = this.props.customMapping[
                                    Object.keys(this.props.customMapping)
                                        .find(k => k.toLowerCase() === objectType.toLowerCase())
                                ];

                                if(objectTypeMapping && Object.keys(objectTypeMapping).length > 0) {
                                    objectHasCustomMapping = true;
                                }
                            }

                            this.setState({
                                selectedDataIndex: selectedDataIndex,
                                objectHasCustomMapping: objectHasCustomMapping
                            });
                        }}
                        excludeIds={['graxgrax_loggingindex']}
                        onRequestRemoveSelectedOption={(event, data) => {
                            this.setState({
                                retrieved: false,
                                selectedDataIndex: null,
                                _graxFieldsSelection: null,
                                _graxFieldsSelectionList: null,
                                objectHasCustomMapping: false
                            });
                        }}
                    />

                    {this.state.objectHasCustomMapping ? (
                        <div>
                            <Alert
                                dismissible
                                labels={{
                                    heading: 'The selected object has a custom mapping defined. To review this custom mapping, click on the "Restore Mapping" tab and then select the object.'
                                }}
                                onRequestClose={() => {
                                    this.setState({ objectHasCustomMapping: false });
                                }}
                            />
                        </div>
                    ) : null}

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>
                
                <SLDSSection isCollapsible={true} isOpen={true} title="Search GRAX Table">
                    {
                        this.state.selectedDataIndex != null
                        ? (
                            <Suspense
                                fallback={(
                                    <div style={{ position: 'relative', height: '2rem' }}>
                                        <Spinner size="small" variant="base" />
                                    </div>
                                )}
                            >
                                <GRAXFieldsCombobox
                                    dataIndex={this.state.selectedDataIndex.id}
                                    onLoad={(e, _graxFieldsSelection, _graxFields, _graxFieldsSelectionList) => {
                                        console.log('onLoad', _graxFieldsSelection, _graxFieldsSelectionList, _graxFields);

                                        this.setState({
                                            _graxFields: _graxFields,
                                            _graxFieldsSelection: _graxFieldsSelection,
                                            _graxFieldsSelectionList: _graxFieldsSelectionList,
                                            _graxSortableFields: _.reduce(_graxFields, (sortableFields, elasticField, i) => {
                                                if(ELASTIC_SORTABLE_TYPES.indexOf(elasticField.type) !== -1) {
                                                    sortableFields.push(elasticField.id);
                                                }
                                
                                                return sortableFields;
                                            }, [])
                                        });
                                    }}
                                    onChange={(e, _graxFieldsSelection, _graxFields, _graxFieldsSelectionList) => {
                                        console.log('onChange', _graxFieldsSelection, _graxFieldsSelectionList);

                                        var retrievedCounter = this.state.retrievedCounter;

                                        if(_graxFieldsSelectionList.length > this.state._graxFieldsSelectionList.length) {
                                            retrievedCounter++;
                                        }

                                        this.setState({
                                            retrievedCounter: retrievedCounter,
                                            _graxFieldsSelection: _graxFieldsSelection,
                                            _graxFieldsSelectionList: _graxFieldsSelectionList
                                        });
                                    }}
                                />
                            </Suspense>
                        )
                        : 'No GRAX Table selected...'
                    }

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <SLDSSection isCollapsible={true} isOpen={true} title="Filter Records">
                    <GRAXRestoreArchiveFilter onChange={(val) => {
                        var archiveFilterQuery;

                        if(val === BOTH) {
                            archiveFilterQuery = null;
                        }

                        if(val === BACKEDUP) {
                            archiveFilterQuery = '(NOT doc.IsDeleted:true)';
                        }

                        if(val === ARCHIVED) {
                            archiveFilterQuery = '(doc.IsDeleted:true)';
                        }

                        this.setState({
                            _archiveFilterQuery: archiveFilterQuery,
                            retrievedCounter: this.state.retrievedCounter + 1
                        })
                    }} />

                    {/*<Input
                        iconLeft={
                            <InputIcon
                                assistiveText="Search"
                                name="search"
                                category="utility"
                            />
                        }
                        assistiveText={{ label: 'Search Term' }}
                        id="record-query"
                        placeholder="Search"
                    />*/}
                    {this.state._graxFieldsSelectionList != null && (
                        <GRAXFilterGeneration
                            SObjectType={this.state.selectedDataIndex.label}
                            filterableFieldOptions={this.state._graxFields}
                            onFilterValueChangeHandler={(_filterFunction, _filterValuesFields, _getFilterQuery) => {
                                this.setState({
                                    retrievedCounter: this.state.retrievedCounter,
                                    _filterFunction: _filterFunction,
                                    _getFilterQuery: _getFilterQuery,
                                    _graxFieldsSelectionList: this.state._graxFieldsSelectionList,
                                    _filterValuesFields: _filterValuesFields
                                });
                            }}
                        />
                    )}

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <SLDSSection isCollapsible={true} isOpen={true} title="GRAX Stored Records">
                    <div className="-x-small-buttons--horizontal slds-p-bottom_small">
                        <Button label="Retrieve" variant="brand" onClick={() => {
                            this.setState({
                                retrieved: this.state.selectedDataIndex != null,
                                retrievedCounter: this.state.retrievedCounter + 1
                            })
                        }} disabled={!this.hasFieldSelected()} />
                    </div>

                    {this.state.retrieved && this.state._graxFieldsSelectionList && (
                        <Suspense
                            fallback={(
                                <div style={{ position: 'relative', height: '2rem' }}>
                                    <Spinner size="small" variant="base" />
                                </div>
                            )}
                        >
                            <GRAXRestoreDataTable
                                key={'GRAXRestoreDataTable_' + this.state.retrievedCounter}
                                renderKey={'GRAXRestoreDataTable_' + this.state.retrievedCounter}
                                columns={this.state._graxFieldsSelectionList}
                                _graxFieldsSelection={this.state._graxFieldsSelection}
                                hiddenColumns={this.state._filterValuesFields}
                                dataIndex={this.state.selectedDataIndex.id}
                                sfRequestsDisabled={this.state._sfRequestsDisabled}
                                restoreChildrens={this.state._restoreChildrens}
                                checkIsUserActive={this.state._checkIsUserActive}
                                useGRAXHistoryOnly={this.state._useGRAXHistoryOnly}
                                updateRecordIfExist={this.state._updateRecordIfExist}
                                sendNotification={this.state._sendNotification}
                                filterFunction={this.state._filterFunction}
                                filterQuery={this.getFilterQuery()}
                                sortableColumns={this.state._graxSortableFields}
                            />
                        </Suspense>
                    )}
                </SLDSSection>
            </div>
        );
    }
}